.App {
 
  display: grid;
  place-items: center;

}

body {

  margin: 0%;
  padding: 0%;
  font-family: montserrat, arial, verdana;
background-color: #D0F0C0;
}

.form {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #D0F0C0;

}
.form2{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #D0F0C0;

}

.form-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.4);
  background-color: beige;

}
.login-container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.4);
  background-color: beige;
margin-top: 200px;
width: 400px;
height: 400px;}
.login-container input{
  margin: 5px;
  width: 200px;
  height: 40px;
  padding-left: 20px;
  font-size: 20px;
}

.form-container .header {
  flex: 20%;
  display: grid;
  place-items: center;
  background-color: beige;
  
}

.form-container .body {
  flex: 60%;
}

.form-container .footer {
  flex: 20%;
  display: flex;
  justify-content: center;
  background-color: beige;

}

.form-container .footer button , .login-container  button {
  border-radius: 7px;
  width: 100px;
  height: 40px;
  background-color:#1E5631;
  font-weight: bold;
  color: white;
  border: 0 none;
  border-radius: 10;
  cursor: pointer;
  padding: 10px 5px;
  margin: 10px 5px;
  margin: 5px;
}

.sign-up-container {
  width: 400px;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  }

.sign-up-container input {
  margin: 5px;
  width: 200px;
  height: 40px;
  padding-left: 20px;
  font-size: 20px;
}

.personal-info-container {

  width: 900px;
  height: 450px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.personal-info-container input {
  margin: 5px;
  width: 200px;
  height: 40px;
  padding-left: 5px;
  font-size: 20px;
}

/* PROGRESS BAR */
.progressbar {
  width: 700px;
  height: 10px;
  background-color: white;
  margin-bottom: 30px;
  margin-top: 30px;
}

.progressbar div {
  width: 33.3%;
  height: 100%;
  background-color: #1E5631;
}

.other-info-container {
  background-color: beige;

  width: 400px;
  height: auto;
   
  display: flex;
  flex-direction: column;
   justify-content: center;
  align-items: center;}

.other-info-container input {
  margin: 5px;
  width: 200px;
  height: 40px;
  padding-left: 5px;
  font-size: 20px;
}
.Formation-container {
  background-color: beige;
  width: 400px;
  height: auto;
   
  display: flex;
  flex-direction: column;
   justify-content: center;
  align-items: center;}

.Formation-container input {
  margin: 5px;
  width: 200px;
  height: 40px;
  padding-left: 5px;
  font-size: 20px;
}


input {
  border: 2px solid #1E5631;
  border-radius: 5px;
}

input:focus {
  border: 3px solid #1E5631;
}
label{
  display: block;
  font-weight: bold;
  width: 200px;
  float: left;
}
.topbar {
  width: 100%;
  height: 50px;
  background-color: #358856;
  position: sticky;
  top: 0;
  z-index: 999;
}

.topbarWrapper {
  height: 100%;
  padding: 0px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  font-weight: bold;
  font-size: 30px;
  color: darkblue;
  cursor: pointer;
}

.topRight{
    display: flex;
    align-items: center;
}

.topbarIconContainer {
  position: relative;
  cursor: pointer;
  margin-right: 10px;
  color: #555;
}

.topIconBadge{
    width: 15px;
    height: 15px;
    position: absolute;
    top: -5px;
    right: 0px;
    background-color: red;
    color: white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;
}

.topAvatar{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    cursor: pointer;
}

.sidebar {
flex: 1;
  height: calc(100vh - 50px);
float: left;
  top: 50px;
  background-color: #5aab61;

}

.sidebarWrapper {
  padding: 20px;
  color: #555;

}

.sidebarMenu {
  margin-bottom: 10px;

}

.sidebarTitle {
  font-size: 13px;
  color: rgb(187, 186, 186);
}

.sidebarList {
  list-style: none;
  padding: 5px;

}

.sidebarListItem {
  padding: 5px;
  cursor: pointer;
  display: flex;
  align-items: center;
  border-radius: 10px;

}

.sidebarListItem.active,
.sidebarListItem:hover {
  background-color: rgb(240, 240, 255);
}

.sidebarIcon{
    margin-right: 5px;
    font-size: 20px !important;
}

